<template>
    <main>
        <Navbar></Navbar>

        <section class="layout-wrapper">
            <router-view></router-view>
        </section>

        <ImagePreviewContainer>
        </ImagePreviewContainer>

        <LazyLoadImageParent />

        <!--<DroplistContainer></DroplistContainer>-->

        <transition name="fade">
            <div class="unfocused-background" v-show="unfocusedBackground.isVisible"
                 v-bind:style="{'z-index': unfocusedBackground.zIndex}"
                 v-on:click="onBackgroundClick"></div>
        </transition>
    </main>
</template>

<script>
import Navbar from "./views/components/Navbar";
import ImagePreviewContainer from "./views/components/ImagePreviewContainer";
import {Events, EventBus} from "./common/eventBus";
import LazyLoadImageParent from "./views/components/lazyLoadImage/LazyLoadImageParent";

export default {
    name: 'app',

    components: {LazyLoadImageParent, ImagePreviewContainer, Navbar},

    data: () => {
        return {
            unfocusedBackground: {
                zIndex: 0,
                isVisible: false,
                isHidingBlocked: false,
            }
        }
    },

    methods: {
        onBackgroundClick() {
            if (this.unfocusedBackground.isVisible) {
                EventBus.$emit(Events.onBackgroundClick);
            }
        },
    },

    created() {
        EventBus.$on(Events.showUnfocusedBackground, (data) => {
            this.unfocusedBackground.zIndex = data.zIndex;

            if (!this.unfocusedBackground.isVisible) {
                this.unfocusedBackground.isVisible = true;
            }
        });

        EventBus.$on(Events.hideUnfocusedBackground, () => {
            if (this.unfocusedBackground.isHidingBlocked) {
                this.unfocusedBackground.isHidingBlocked = false;
            } else {
                this.unfocusedBackground.isVisible = false;
            }
        });

        EventBus.$on(Events.blockHidingEvent, (zIndex) => {
            this.unfocusedBackground.isHidingBlocked = true;
            this.unfocusedBackground.zIndex = zIndex;
        })
    },

    destroyed() {
        EventBus.$off(Events.showUnfocusedBackground);
        EventBus.$off(Events.hideUnfocusedBackground);
        EventBus.$off(Events.blockHidingEvent);
    }
}
</script>

<style lang="sass">

    .fade-enter-active, .fade-leave-active
        transition: 0.35s

    .fade-enter, .fade-leave-to
        opacity: 0

    .layout-wrapper
        margin: 200px auto auto
        position: relative

    .unfocused-background
        position: fixed
        width: 100%
        height: 100%
        top: 0
        left: 0
        transition: 0.35s
        background-color: rgba(0, 0, 0, 0.5)!important
</style>
