export const HOME_URL = () => {
    let protocol = window.location.protocol;
    let hostname = window.location.hostname;
    let port = window.location.port;

    if (port.length !== 0) {
        return protocol + '//' + hostname + ':' + port
    } else {
        return protocol + '//' + hostname
    }
};

export const API_URL = () => {
    return "https://donopttorg-api.herokuapp.com" + "/api"
};

export const PROTECTED_API_URL = () => {
    return API_URL() + "/protected"
};

export const WS_API_URL = () => {
    let hostname = window.location.hostname;
    let port = window.location.port;

    if (port.length !== 0) {
        return "http:" + '//' + hostname + ':' + port + "/api"
    } else {
        return "http:" + '//' + hostname + "/api"
    }
};

export default {HOME_URL, API_URL, WS_API_URL, PROTECTED_API_URL}