const imageFetch = {
    options: {
        method: 'GET',
    },

    imageURL(imgId) {
        return "images/" + imgId
    },

    fullSizeImageURL(imgId) {
        return "images/fullSize/" + imgId
    },

    async download(url) {
        return await fetch(url, this.options);
    },

    parse(arrayBuffer) {
        let binary = '',
            bytes = [].slice.call(new Uint8Array(arrayBuffer));

        bytes.forEach((b) => binary += String.fromCharCode(b));
        return 'data:image/jpeg;base64,' + window.btoa(binary);
    },

};

export default imageFetch;