<template>

    <section>
        <section id="header-wrapper" class="header-wrapper">
            <section class="header">

                <div class="top fade-on-minify">
                    <p class="title">{{name}} </p>
                    <img src="/svgs/cog.svg" v-on:click="$refs.dialog.show()" />
                    <!--<img class="logo" src="/logo.png"/>-->
                </div>

                <div class="bottom">

                    <div class="center" id="trigger" v-if="!isSearching">
                        <router-link :to="{ name: 'home'}">Главная</router-link>
                        <router-link :to="{ name: 'categoryChoose'}">Каталог</router-link>
                        <router-link :to="{ name: 'waybills'}">Накладные</router-link>
                        <!--<i class="fas fa-search clickable"></i>-->
                        <a v-on:click="showSearch">Поиск Товара</a>
                    </div>

                    <div class="search" v-show="isSearching" v-bind:style="{opacity: searchingOpacity}">
                        <input id="searchInput" placeholder="Поиск Товара" type="text" autocorrect="off" autocapitalize="off"
                               spellcheck="false" @input="onInput" @keyup.enter="search($event.target.value)" ref="input"/>
                        <img  v-on:click="hideSearch" class="fa-search" src="/svgs/times.svg"/>
                    </div>

                    <section class="searching-helper" v-if="isSearching && searchHelperResults.length !== 0" v-bind:style="{opacity: searchingOpacity}">
                        <p class="item" v-for="item in searchHelperResults" v-on:click="search(item)">{{item}}</p>
                    </section>

                </div>

            </section>

        </section>

        <Dialog ref="dialog">
            <Setting />
        </Dialog>
    </section>

</template>

<script>
    import {mapGetters} from "vuex";
    import modules from "../../store/all.modules.name"
    import {EventBus, Events} from "../../common/eventBus";
    import {CatalogAPI} from "../../api/catalog";
    import Dialog from "./Dialog";
    import Setting from "./Settings";

    export default {
        name: "Navbar",

        components: {Setting, Dialog},

        data: () => {
            return {
                triggerY: 0,

                searchHelperResults: [],

                isMinified: false,
                isSearching: false,
                isSearchingTemp: false,
                searchingOpacity: 0,
            }
        },

        methods: {
            showSearch() {
                this.isSearching = true;
                this.isSearchingTemp = true;
                EventBus.$emit(Events.showUnfocusedBackground, {zIndex: 9});
                EventBus.$on(Events.onBackgroundClick, () => {
                    this.hideSearch();
                });

                setTimeout(() => {
                    this.searchingOpacity = 1;
                    if (!document.getElementById("header-wrapper").classList.contains("minified")) {
                        this.isMinified = true;
                        window.scrollTo(0, this.triggerY);
                        document.getElementById("header-wrapper").classList.add("minified");
                    }
                    this.$refs.input.focus();
                }, 50);
            },

            hideSearch() {
                this.searchingOpacity = 0;
                this.searchHelperResults = [];
                document.getElementById("searchInput").value = "";
                EventBus.$emit(Events.hideUnfocusedBackground);
                EventBus.$off(Events.onBackgroundClick);
                setTimeout(() => {
                    this.isSearching = false;
                    if (this.triggerY > this.currentY()) {
                        this.isMinified = false;
                        document.getElementById("header-wrapper").classList.remove("minified");
                    }
                }, 250);
            },

            onInput(event) {
                let value = event.target.value;
                if (value.length !== 0) {
                    CatalogAPI.getSearchHelper(value).then((res) => {

                        if (res !== null) {
                            res.json().then(data => {
                                this.searchHelperResults = data;
                            });
                        } else {
                            this.searchHelperResults = [];
                        }

                    });
                } else if (this.searchHelperResults.length !== 0) {
                    this.searchHelperResults = [];
                }
            },

            search(value) {
                if (value.length !== 0) {
                    this.$router.push({ name: 'search', params: { request: value } });
                    this.searchHelperResults = [];
                    this.hideSearch();
                    document.getElementById("searchInput").value = "";
                }
            },

            handleScroll() {
                if (this.isSearching) {

                    if (this.isSearchingTemp) {
                        this.isSearchingTemp = false;
                    } else {
                        this.hideSearch();
                    }
                } else if(this.currentY() !== this.triggerY) {
                    let parent  = document.getElementById("header-wrapper");

                    if (this.triggerY <= this.currentY() && !this.isMinified)  {
                        this.isMinified = true;
                        parent.classList.toggle("minified");
                    } else if (this.currentY() <= this.triggerY && this.isMinified) {
                        this.isMinified = false;
                        parent.classList.toggle("minified");
                    }
                }
            },

            currentY() {
                return (window.pageYOffset || document.documentElement.scrollTop) - (document.documentElement.clientTop || 0);
            }
        },

        computed: {
            ...mapGetters(modules.user, [
                "name"
            ])
        },

        created() {
            window.addEventListener('scroll', this.handleScroll);
            this.triggerY = 146;
        },

        destroyed() {
            window.removeEventListener('scroll', this.handleScroll);
        }
    }
</script>

<style lang="sass" scoped>
    @import '../../assets/basic'

    .unfocused-background
        position: fixed
        width: 100%
        height: 100%
        top: 0
        left: 0
        z-index: 10
        transition: 0.35s
        background-color: rgba(0, 0, 0, 0.5)!important

    .search
        display: block
        height: 20px
        transition: 0.25s

        input
            width: 100%
            height: 100%
            border: none
            font-size: 22px

            &:focus
                outline: none

        .fa-search
            position: absolute
            width: 20px
            height: 20px
            cursor: pointer
            right: 0
            font-size: 18px
            top: 50%
            transform: translateY(-50%)


    .minified
        box-shadow: 0 0 6px rgba(0,0,0,0.5)
        position: fixed!important

        .header
            padding-top: 0!important

            .bottom
                padding-top: 1rem !important
                padding-bottom: 1rem!important

                .searching-helper
                    position: absolute
                    width: 100%
                    top: 52px
                    box-shadow: 0 20px 20px 0 #0000003d
                    border-bottom-left-radius: 5px
                    border-bottom-right-radius: 5px
                    background-color: $background-color
                    transition: 0.25s

                    p
                        font-size: 18px
                        padding-left: 10px
                        margin: 0
                        padding-top: 1rem
                        padding-bottom: 1rem

                        &:hover
                            background-color: #f2f2f2
                            cursor: pointer

                        &:last-child
                            border-bottom-left-radius: 5px
                            border-bottom-right-radius: 5px

            .fade-on-minify
                display: none!important


    .header-wrapper
        top: 0
        right: 0
        width: 100%
        background-color: $background-color
        z-index: 10
        position: absolute
        transition: 0.5s
        font-family: Roboto, sans-serif

        .header
            max-width: 750px
            margin: auto
            padding-top: 2rem

            a
                text-decoration: none
                cursor: pointer

                &:visited
                    color: black

                &:link
                    color: black

            .top
                display: flex
                justify-content: center

                img
                    width: 16px
                    height: 16px

                .title
                    margin: 0
                    font-family: Oswald, sans-serif
                    font-size: 2rem
                    background-color: $secondary-color
                    color: $background-color
                    padding: 5px 10px 5px


                .logo
                    display: block
                    margin-left: auto
                    margin-right: auto

            .bottom
                display: block
                position: relative
                max-width: 700px
                margin: auto
                padding: 1.6875rem 0 0
                //background-color: red

                .small-spaces
                    li
                        margin: 0 .25rem!important
                        padding: 0 .375rem!important

                .center
                    display: grid
                    grid-template-columns: repeat(4, 1fr)
                    //background-color: blue

                    a
                        font-family: Roboto,sans-serif
                        font-size: 17px
                        font-weight: 500
                        margin: 0 .28rem
                        padding: 0 .375rem
                        text-align: center

                    .fa-search
                        margin-top: 1px
                        width: 14px
                        height: 14px

                .right
                    //background-color: green
                    position: absolute
                    right: 0
                    margin-left: auto



</style>