import {additionalWaybillData} from "../../../common/testingData";
import date from "../../../common/date"

import {
    LOAD_WAYBILLS,
    LOAD_MORE_WAYBILLS,
    GET_EXTENDED_WAYBILL
} from "./actions.type";

import {
    ADD_WAYBILLS,
    FINISH_LOADING, HAS_MORE_TO_LOAD,
    SET_WAYBILLS,
    START_LOADING
} from "./mutations.type";

import {WaybillsAPI} from "../../../api/waybills";

export const state = {
    waybills: [],
    isLoading: false,
    hasMoreToLoad: true,
};

export const actions = {
    async [LOAD_WAYBILLS](state) {
        state.commit(START_LOADING);

        let res = await WaybillsAPI.getAllWaybills("none", 10);
        if (res !== null) {
            let data = await res.json();

            data.forEach((item) => {
                if (item.deliveryDateUnix) {
                    item.deliveryDate = date.toCoolDate(date.unixTimeStampToDate(item.deliveryDateUnix))
                }
            });

            state.commit(SET_WAYBILLS, data);
        }

        state.commit(FINISH_LOADING);
    },

    async [LOAD_MORE_WAYBILLS](state) {
        state.commit(START_LOADING);

        let res = await WaybillsAPI.getAllWaybills(state.getters.getLastWaybillId, 10);
        if (res !== null) {
            let data = await res.json();

            data.forEach((item) => {
                if (item.deliveryDateUnix) {
                    item.deliveryDate = date.toCoolDate(date.unixTimeStampToDate(item.deliveryDateUnix))
                }
            });
            state.commit(ADD_WAYBILLS, res.data);

            if (data.length < 10) {
                state.commit(HAS_MORE_TO_LOAD, false);
            }
        }

        state.commit(FINISH_LOADING);
    },

    async [GET_EXTENDED_WAYBILL](state, waybillId) {
        state.commit(START_LOADING);

        let res = await WaybillsAPI.getExtendedWaybill(waybillId);
        if (res !== null) {
            state.commit(FINISH_LOADING);
            let resData = await res.json();
            resData.products.sort((a, b) => {
                return a.orderNumber - b.orderNumber;
            });
            return resData;
        } else {
            state.commit(FINISH_LOADING);
            return null
        }
    }
};

export const mutations = {
    [ADD_WAYBILLS](state, data) {
        state.waybills = [...state.waybills, ...data]
    },

    [SET_WAYBILLS](state, data) {
        state.waybills = data;
    },

    [START_LOADING](state) {
        state.isLoading = true;
    },

    [FINISH_LOADING](state) {
        state.isLoading = false;
    },

    [HAS_MORE_TO_LOAD](state, data) {
        state.hasMoreToLoad = data;
    }
};

export const getters = {
    getWaybillById: state => id => {
        for (let i = 0; i < state.waybills.length; i++) {
            if (state.waybills[i].id === id) {
                return { ...state.waybills[i], ...additionalWaybillData};
            }
        }

        return null;
    },

    waybills(state) {
        return state.waybills.map(w => {
            w.isActive = w.deliveryDateUnix? date.isAfter(
                date.unixTimeStampToDate(w.deliveryDateUnix),
                date.getCurrentDate()) : true;

            return w;
        })
    },

    activeWaybills(state) {
        return state.waybills.filter(w => w.deliveryDateUnix ?
            date.isAfter(date.unixTimeStampToDate(w.deliveryDateUnix), date.getCurrentDate()) : true
        );
    },

    inactiveWaybills(state) {
        return state.waybills.filter(w => w.deliveryDateUnix ?
            !date.isAfter(date.unixTimeStampToDate(w.deliveryDateUnix), date.getCurrentDate()) : false
        );
    },

    getLastWaybillId(state) {

        if (state.waybills.length !== 0) {
            return state.waybills[state.waybills.length - 1].id;
        }
    },

    hasWaybills(state) {
        return state.waybills.length !== 0
    },

    hasMoreToLoad(state) {
        return state.hasMoreToLoad
    },

    isLoading(state) {
        return state.isLoading;
    }
};

export default {
    state,
    actions,
    mutations,
    getters,
    namespaced: true,
}