const myCacheStorage = (name) =>  {
    if ('caches' in self) {
        return {
            name: name,

            openCache() {
                return caches.open(name)
            },

            add(url, response, lifeTime) {
                response = new Response(response.body, response);
                response.headers.append('expirationDate', new Date().getTime() + (lifeTime * 1000));

                this.openCache().then(cache => {
                    cache.put(url, response);
                });
            },

            async updateJSON(url, data) {
                let cache = await this.openCache(),
                    res = await cache.match(url),
                    expDate = JSON.parse(res.headers.get("expirationDate"));

                if (res) {
                    cache.put(url, new Response(JSON.stringify(data), {
                        headers: {
                            'Content-Type': 'application/json',
                            'expirationDate': expDate,
                        },
                    }));
                }
            },

            async get(request) {
                let cache = await this.openCache();
                let res = await cache.match(request);

                if (res) {
                    if (new Date().getTime() - JSON.parse(res.headers.get("expirationDate")) < 0) {
                        res = new Response(res.body, res);
                        res.isFromCache = true;
                        return res
                    } else {
                        cache.delete(request);
                        return null;
                    }
                } else {
                    return null;
                }
            },

            async has(url) {
                let res = await this.get(url);
                return res !== null && res !== undefined;
            },

            clean() {
                caches.delete(name)
                    .then(() => {
                        console.log(`Cache(${name}) successfully deleted!`);
                    })
                    .catch((error) => {
                        console.log(`Error during deleting Cache(${name}): \n${error}`);
                    })
            },

            isSupported() {
                return 'caches' in self
            },
        }
    } else {
        return null;
    }
};

export default myCacheStorage;