<template>
    <section class="dialog-wrapper" v-if="isVisible">

        <section class="dialog" v-bind:style="{height: height + 'px', opacity}">
            <header>
                <img src="/svgs/arrow-left-white.svg" v-on:click="$emit('return')" v-if="canReturn"/>
                <img src="/svgs/times-white.svg" class="right" v-on:click="hide" />
            </header>

            <section class="content-wrapper" v-bind:style="{height: (height - 20) + 'px'}">
                <slot v-if="isInnerVisible"></slot>
                <section class="center" v-if="!isInnerVisible">
                    <LoadingSpinner></LoadingSpinner>
                </section>
            </section>

        </section>

    </section>
</template>

<script>
    import {EventBus, Events} from "../../common/eventBus";
    import LoadingSpinner from "./LoadingSpinner";

    export default {
        name: "Dialog",

        components: {LoadingSpinner},

        props: {
            canReturn: {
                type: Boolean,
                default: false,
            }
        },

        data: () => {
            return {
                height: 0,
                isVisible: false,
                isInnerVisible: false,
                hasAnotherLayoutOnTopLevel: false,
                opacity: 0,
            }
        },

        methods: {
            show() {
                EventBus.$emit(Events.showUnfocusedBackground, {zIndex: 15});
                EventBus.$on(Events.onBackgroundClick, () => this.onBackgroundClick());

                this.isVisible = true;
                setTimeout(() => {
                    this.opacity = 1;
                    setTimeout(() => {
                        this.isInnerVisible = true;
                    }, 350);
                }, 50);
            },

            hide() {
                EventBus.$emit(Events.hideUnfocusedBackground, {zIndex: 15});
                EventBus.$off(Events.onBackgroundClick);

                this.opacity = 0;
                this.isInnerVisible = false;
                setTimeout(() => {
                    this.isVisible = false;
                    this.$emit('hide');
                }, 350);
            },

            onBackgroundClick() {
                console.log("on background click");
                if (!this.hasAnotherLayoutOnTopLevel) {
                    this.hide();
                } else {
                    EventBus.$emit(Events.blockHidingEvent, 15);
                    this.hasAnotherLayoutOnTopLevel = false;
                    setTimeout(() => {
                        EventBus.$on(Events.onBackgroundClick, () => this.onBackgroundClick());
                    }, 350);
                }
            },
        },

        mounted() {
            this.height = Math.round(window.innerHeight * 0.7);
        },

        created() {
            EventBus.$on(Events.showImagePreview, () => {
                this.hasAnotherLayoutOnTopLevel = true;
            });
        },

        destroyed() {
            EventBus.$off(Events.showImagePreview);
        },
    }
</script>

<style lang="sass" scoped>
    .dialog-wrapper
        z-index: 16
        width: 100%
        position: fixed
        right: auto
        left: auto
        top: 50%
        -ms-transform: translateY(-50%)
        transform: translateY(-50%)

        .center
            display: flex
            position: absolute
            justify-content: center
            top: 50%
            left: 50%
            transform: translate(-50%, -50%)

        .dialog
            position: absolute
            box-shadow: 0 3px 6px rgba(0, 0, 0, 0.50), 0 3px 6px rgba(0, 0, 0, 0.50)
            padding-top: 25px
            padding-bottom: 10px
            transition: 0.35s
            width: 90%
            max-width: 900px
            height: 220px
            border-radius: 15px
            background-color: white
            top: 50%
            left: 50%
            -ms-transform: translate(-50%, -50%)
            transform: translate(-50%, -50%)

            header
                display: flex
                position: absolute
                right: 0
                left: 0
                top: 0
                height: 20px
                border-top-right-radius: 15px
                border-top-left-radius: 15px
                background-color: #1D5DB5
                padding: 2.5px 20px

                img
                    cursor: pointer
                    width: 20px
                    height: 20px

                .right
                    margin-left: auto

            .content-wrapper
                height: 200px
                padding-top: 10px
                overflow-x: hidden
                overflow-y: scroll

                &::-webkit-scrollbar
                    display: none
</style>