import APIService from "./index";
import config from "../common/config";

export const CatalogAPI = {

    getAllCategories: () => {
        return APIService.postWithCaching("/catalog/getAllCategories", {}, 3600)
    },

    getProductsByCategory: (id, subCategoryId) => {

        if (subCategoryId) {
            return APIService.postWithCaching("/catalog/getProductsByCategory", {id, subCategoryId}, 3600)
        } else {
            return APIService.postWithCaching("/catalog/getProductsByCategory", {id}, 3600)
        }
    },

    getSearchHelper: (request) => {
        return APIService.post("/catalog/searchHelper", {request})
    },

    search: (request) => {
        return APIService.postWithCaching("/catalog/search", {request}, 60)
    },

    createProductsCountInStockUpdater() {
        return {
            socket: new WebSocket(config.WS_API_URL() + "/catalog/countInStock"),

            sendJSONMessage(message, callback) {
                this.socket.send(JSON.stringify(message));
                if (callback) {
                    this.socket.onmessage = callback;
                }
            }
        }
    }
};