import Vue from "vue";
import Router from "vue-router";
import {CLEAR_PRODUCTS} from "../store/modules/catalog/mutations.type";
import store from "../store";
import modules from "../store/all.modules.name"

Vue.use(Router);

const router = new Router({
    mode: 'history',

    routes: [
        {
            path: "/",
            name: 'home',
            component: () => import(/* webpackChunkName: "home" */ "@/views/Home")
        },
        {
            path: "/catalog",
            name: 'categoryChoose',
            component: () => import(/* webpackChunkName: "catalog" */ "@/views/CategoryChoose")
        },
        {
            path: "/catalog/:categoryId",
            name: 'catalog',
            component: () => import(/* webpackChunkName: "catalog" */ "@/views/Catalog")
        },
        {
            path: "/catalog/:categoryId/:subCategoryId",
            name: 'catalogWithSub',
            component: () => import(/* webpackChunkName: "catalog" */ "@/views/Catalog")
        },
        {
            path: "/search/:request",
            name: 'search',
            component: () => import(/* webpackChunkName: "catalog" */ "@/views/Catalog")
        },
        {
            path: "/waybills",
            name: "waybills",
            component: () => import(/* webpackChunkName: "waybills" */ "@/views/Waybills")
        },
        {
            path: "/waybill/:id",
            name: "waybill",
            component: () => import(/* webpackChunkName: "waybill" */ "@/views/Waybill")
        },
        {
            path: "/ordering",
            name: "ordering",
            component: () => import(/* webpackChunkName: "ordering" */ "@/views/Ordering")
        },
        {
            path: "/login",
            name: "login",
            component: () => import(/* webpackChunkName: "login" */ "@/views/Login")
        },
        {
            path: "/admin/news/create",
            name: "adminNewsCreate",
            component: () => import(/* webpackChunkName: "adminNews" */ "@/views/AdminNewsEditor")
        }
    ],
});


router.beforeEach((to, from, next) => {
    if (from.name === "catalog") {
        store.dispatch(modules.catalog + "/" + CLEAR_PRODUCTS, from.params.categoryId)
    }
    next();
});

/*router.afterEach((to, from) => {
});*/

export default router;