import Vue from "vue";
import Vuex from "vuex";

import modules from "./all.modules.name";

import catalog from "./modules/catalog"
import waybills from "./modules/waybills/index"
import user from "./modules/user"

Vue.use(Vuex);

let store = new Vuex.Store({});
store.registerModule(modules.catalog, catalog);
store.registerModule(modules.waybills, waybills);
store.registerModule(modules.user, user);

export default store;

