import APIService from "./index";

export const WaybillsAPI = {
    getAllWaybills: (lastId, numberOfElementsToLoad) => {
        return APIService.post("/waybills/getAllWaybills", {userId: "hello world", lastId, numberOfElementsToLoad})
    },

    getExtendedWaybill: (waybillId) => {
        return APIService.postWithCaching("/waybills/getExtendedWaybill", {waybillId}, 3600)
    },
};