<template>
    <div v-if="isVisible">
        <section style="z-index: 17;position: fixed;">
            <transition name="fade">
                <LoadingSpinner class="center" v-show="isLoading"/>
            </transition>

            <transition name="fade">
                <img class="center" v-show="!isLoading" ref="img"
                     @click="clickHandler"/>
            </transition>
        </section>
    </div>
</template>

<script>
    import {EventBus, Events} from "../../common/eventBus";
    import {API_URL} from "../../common/config";
    import myCacheStorage from "../../common/cache"
    import imageFetch from "../../common/imageFetch"
    import LoadingSpinner from "./LoadingSpinner";

    const cache = myCacheStorage('fullSizeImages');

    export default {
        name: "ImagePreviewContainer",

        components: {LoadingSpinner},

        data: () => {
            return {
                isVisible: false,
                isLoading: false,
                img: "",
            }
        },

        methods: {
            clickHandler() {
                EventBus.$emit(Events.hideUnfocusedBackground, {zIndex: 15});
                this.hide();
                EventBus.$off(Events.onBackgroundClick);
            },

            showImage(img) {
                console.log("show img:", {img});
                this.isLoading = true;
                let url = this.getUrl(img);

                this.show();
                let download = () => {
                    imageFetch.download(url)
                        .then((res) => {
                           if (res !== null) {
                               cache.add(url, res.clone(), 3600);
                               return res.arrayBuffer()
                           } else {
                               throw "image not found";
                           }
                        })
                        .then((buffer) => {
                            let res = imageFetch.parse(buffer);
                            this.isLoading = false;
                            this.$refs.img.src = res;
                        })
                        .catch((error) => {
                            console.error(error);
                        });
                };

                if (cache !== null) {
                    cache.has(this.getUrl(img))
                        .then((has) => {
                            if (has) {
                                cache.get(url)
                                    .then(res => res.arrayBuffer())
                                    .then((buffer) => {
                                        this.isLoading = false;
                                        this.$refs.img.src = imageFetch.parse(buffer);
                                    })
                            } else {
                                download();
                            }
                        });
                } else {
                    download();
                }

                EventBus.$emit(Events.showUnfocusedBackground, {zIndex: 16});
                EventBus.$on(Events.onBackgroundClick, () => {
                    EventBus.$emit(Events.hideUnfocusedBackground, {zIndex: 16});
                    this.hide();
                    EventBus.$off(Events.onBackgroundClick);
                })
            },

            show() {
                this.isVisible = true;
            },

            hide() {
                this.isVisible = false
            },

            getUrl(img) {
                return API_URL() + "/" + img;
            },
        },

        created() {
            EventBus.$on(Events.showImagePreview, (img) => this.showImage(img));

            window.addEventListener('scroll', () => {
                if (this.isVisible) {
                    EventBus.$emit(Events.hideUnfocusedBackground, {zIndex: 16});
                    this.hide();
                    EventBus.$off(Events.onBackgroundClick);
                }
            });
        },

        destroyed() {
            EventBus.$off(Events.showImagePreview);
        },
    }
</script>

<style lang="sass" scoped>

    .fade-enter-active, .fade-leave-active
        transition: 0.35s

    .fade-enter, .fade-leave-to
        opacity: 0

    img
        box-shadow: 0 14px 28px rgba(0, 0, 0, 0.52), 0 10px 10px rgba(0, 0, 0, 0.5)

    .center
        margin: auto
        position: fixed
        top: 0
        left: 0
        bottom: 0
        right: 0
</style>