<template></template>

<script>
    import {EventBus, Events} from "../../../common/eventBus";
    import {API_URL} from "../../../common/config";
    import myCacheStorage from "../../../common/cache"
    import imageFetch from "../../../common/imageFetch"

    const cache = myCacheStorage('images');
    const maxParallelLoadingsNumber = 3;

    export default {
        name: "LazyLoadImageParent",

        data: () => {
            return {
                dataToLoad: [],

                isWorking: false,

                parallelLoadingsNumber: 0,
            }
        },

        methods: {
            sortDataToRender() {
                this.dataToLoad.sort((a, b) => {
                    return a.priority - b.priority;
                })
            },

            async handleElement(el) {
                let url = this.getUrl(el),
                    rawRes, res;

                if (cache !== null && await cache.has(url)) {
                    rawRes = await cache.get(url);
                    res = await rawRes.text();
                } else {
                    rawRes = await imageFetch.download(this.getUrl(el));
                    res = imageFetch.parse(await rawRes.clone().arrayBuffer());

                    if (cache !== null)
                        await cache.add(url, rawRes, 3600);
                }

                el.callback(res);
                this.parallelLoadingsNumber--;
                this.runHandlers();
            },

            runHandlers() {
                while (this.parallelLoadingsNumber !== maxParallelLoadingsNumber && this.dataToLoad.length !== 0) {
                    this.handleElement(this.dataToLoad[0]);
                    this.dataToLoad.shift();
                    this.parallelLoadingsNumber++;
                }
            },

            getUrl(el) {
                return API_URL() + "/" + el.url;
            },
        },

        created() {
            EventBus.$on(Events.lazyLoadImage, (payload) => {
                let url = this.getUrl(payload);

                if (cache !== null) {
                    cache.has(url).then((has) => {
                        if (has) {
                            cache.get(url)
                                .then((res) => res.arrayBuffer())
                                .then((buffer) => {
                                    payload.callback(imageFetch.parse(buffer))
                                })
                        } else {
                            this.dataToLoad.push(payload);
                            this.runHandlers();
                        }
                    })
                } else {
                    this.dataToLoad.push(payload);
                    this.runHandlers();
                }
            });

            EventBus.$on(Events.lazyLoadChangePriority, (payload) => {
                let el = this.dataToRender().find((element) => {
                    return element.url === payload.url;
                });

                if (el) {
                    el.priority = payload.priority;
                    //this.sortDataToRender();
                }
            });
        },

        destroyed() {
            EventBus.$off(Events.lazyLoadImage);
            EventBus.$off(Events.lazyLoadChangePriority);
        },
    }
</script>

